  methods: {
    orderBegin(value) {
      if (value !== 4 || this.isClick) {
        return;
      }
      this.isClick = true;
      this.auditPlay();
      let num = -1;
      let dir = [0, 1, 2, 5, 8, 7, 6, 3];
      this.current = 20 + Math.floor(Math.random() * 8);
      clearInterval(this.timer);
      this.timer = setInterval(() => {
        num++;
        if (num >= this.current) {
          clearInterval(this.timer);
          this.auditPause();
          this.isClick = false;
        } else {
          this.currentIndex = dir[num % 8];
        }
      }, 100);
    },
    fetchLrc(src) {
      fetch(src)
        .then((response) => response.text())
        .then((lrc) => {
          this.musicBg.lrc = lrc;
        });
    },
    auditPlay() {
      this.auditPauseBg();
      this.$refs.auditPlay.audio.currentTime = 0;
      this.$refs.auditPlay.play();
    },
    auditPause() {
      this.$refs.auditPlay.pause();
    },
    auditPlayBg() {
      this.auditPause();
      this.$refs.auditPlayBg.play();
    },
    auditPauseBg() {
      this.$refs.auditPlayBg.pause();
    },
    goAlbum() {
      this.$router.push("/album");
    },
    confession(value) {
      this.auditPlayBg();
      Dialog.confirm({
        message: value,
        confirmButtonText: "好",
        cancelButtonText: "不好",
